
import { Injectable } from '@angular/core';
import { ComposerService } from '@acaprojects/ngx-composer';

import { BaseService } from '../base.service';

export interface ICateringMenuItem {
    /** Database ID for the item */
    id: string;
    /** Display name of the item */
    name: string;
    /** Display description of the item */
    description: string;
    /** Month of the year the item starts being available */
    from: number
    /** Month of the year the item ends being available */
    to: number
    /** Time in hours before the order that is needed to prepare the item */
    notice: number;
    /** Time in hours before the booking that this item can be purchased  */
    cut_off: number;
    /** Cost of item without decimals. Decimal currencies should be normalised to integers e.g. $123.45 -> 12345 */
    supplier_cost: number;
    /** Price of item without decimals. Decimal currencies should be normalised to integers e.g. $123.45 -> 12345 */
    price: number;
    /** Minimum number of items allowed in an order */
    minimum: number;
    /** Maximum number of items allowed in an order */
    maximum: number;
    /** Allergen details for the item */
    allergy: string;
    /** List of category IDs associated with the item */
    categories: string[];
    /** Number of this item in the order */
    amount?: number;
    /** Whether the item is a selected option of the parent package */
    selected?: boolean;
}


@Injectable({
    providedIn: 'root'
})
export class CateringItemsService extends BaseService<ICateringMenuItem> {

    constructor(protected _composer: ComposerService) {
        super(_composer);
        this.model.name = 'catering item';
        this.model.route = '/catering/item';
    }

    public query(): never {
        throw Error('No index endpoint for catering items. Use menu service.');
    }

    public show(): never {
        throw Error('No show endpoint for catering items. Use menu service.');
    }

    public format(raw: any) {
        const formatted = {
            ...raw,
            parent_categories: raw.parent_categories || raw.parent,
            minimum_quantity: raw.minimum || raw.minimum_quantity,
            maximum_quantity: raw.maximum || raw.maximum_quantity
        };
        delete formatted.minimum;
        delete formatted.maximum;
        return formatted;
    }
}