import { Component, OnDestroy } from '@angular/core';
import { animate, style, transition, trigger } from '@angular/animations';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';

import { AppService } from '../../services/app.service';
import { IBooking } from '../../services/data/bookings.service';
import { IRoom } from '../../services/data/rooms.service';
import { Utils } from '../../shared/utility.class';

import * as dayjs from 'dayjs';

@Component({
    selector: 'meeting-details-overlay',
    templateUrl: './meeting-details-overlay.template.html',
    styleUrls: ['./meeting-details-overlay.styles.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [style({ opacity: 0, height: 0 }), animate(200, style({ opacity: 1, height: '*' }))]),
            transition(':leave', [style({ opacity: 1, height: '*' }), animate(200, style({ opacity: 0, height: 0 }))])
        ])
    ]
})
export class MeetingDetailsOverlayComponent extends OverlayContentComponent<AppService> implements OnDestroy {
    /** Whether to show modal actions tooltip */
    public show_actions: boolean;

    /** Booking to render on the view */
    public get booking(): IBooking {
        return this.model.booking || null;
    }

    /** Title of the booking */
    public get title(): string {
        return (this.booking || ({} as any)).title || '';
    }

    /** Period the booking will occur */
    public get period(): string {
        const booking = this.booking || ({} as any);
        return booking.all_day
            ? 'All Day'
            : `${dayjs(booking.date).format('h:mm A')} - ${dayjs(booking.date)
                  .add(booking.duration, 'm')
                  .format('h:mm A')}`;
    }

    /** Active space to display */
    public get space(): IRoom {
        const booking = this.booking || ({} as any);
        return booking.room || ({} as any);
    }

    /** Requirement notes for the active space */
    public get space_note(): string {
        const booking: IBooking = this.booking || ({} as any);
        const notes = (booking.notes || []).filter(i => i.type === 'equipment' && i.space === this.space.id);
        const note = notes.sort((a, b) => b.date - a.date).map(i => i.message)[0];
        return note;
    }

    /** Expected number of attendees for the active space */
    public get expected(): number {
        const booking: IBooking = this.booking || ({} as any);
        return (booking.expected_attendees || {})[this.space.id] || 0;
    }


    /** Equipment charge code */
    public get code(): string {
        const booking: IBooking = this.booking || ({} as any);
        return (booking.equipment_code || {})[this.space.id] || '';
    }

    /** Catering order for the active space */
    public get catering_order() {
        const booking: IBooking = this.booking || ({} as any);
        return booking.catering
            ? booking.catering.items
                ? booking.catering
                : (booking.catering || {})[this.space.id] || {}
            : {};
    }

    /** Currency code for the active space */
    public get symbol(): string {
        const level = this.space.level || ({} as any);
        const building = this.service.Buildings.get(level.bld_id) || ({} as any);
        return building.currency || 'USD';
    }

    /** Total for the catering order of the active space */
    public get total(): number {
        return this.catering_order.total || 0;
    }

    /** Catering notes for the active space */
    public get catering_note(): string {
        const booking: IBooking = this.booking || ({} as any);
        const notes = (booking.notes || []).filter(i => i.type === 'catering' && i.space === this.space.id);
        const note = notes.sort((a, b) => a.date - b.date).map(i => i.message)[0];
        return note;
    }

    /** Private notes for the active space */
    public get other_note(): string {
        const booking: IBooking = this.booking || ({} as any);
        const notes = (booking.notes || []).filter(i => i.type === 'private' && i.space === this.space.id);
        const note = notes.sort((a, b) => a.date - b.date).map(i => i.message)[0];
        return note;
    }

    /** Type of booking */
    public get type(): string {
        if (!this.booking || !this.booking.booking_type) {
            return '';
        }
        if (this.booking.state === 'cancelled') {
            return 'cancelled'
        }
        return  (this.booking.booking_type as any).id
            ? (this.booking.booking_type as any).id
            : (this.booking.booking_type)
    }

    /** Setup time before the meeting start */
    public get setup(): string {
        const booking: IBooking = this.booking || ({} as any);
        return Utils.humaniseDuration(booking.setup || 0);
    }

    /** Breakdown time after the meeting end */
    public get breakdown(): string {
        const booking: IBooking = this.booking || ({} as any);
        return Utils.humaniseDuration(booking.breakdown || 0);
    }

    /** Whether the booking can be edited */
    public get can_edit(): boolean {
        const now = dayjs();
        const date = dayjs(this.booking.date);
        return now.isBefore(date, 'm');
    }

    /** Whether the booking is in progress */
    public get in_progress(): boolean {
        const now = dayjs();
        const date = dayjs(this.booking.date);
        const end = date.add(this.booking.duration, 'm');
        return !now.isBefore(date, 'm') && now.isBefore(end, 'm');
    }

    /** Whether the booking can be accepted */
    public get can_accept(): boolean {
        return this.booking.state === 'tentative' || this.booking.state === 'cancelled';
    }

    /** Whether the booking can be declined */
    public get can_decline(): boolean {
        return this.booking.state !== 'cancelled';
    }

    public init() {
        this.subs.obs.new_booking = this.service.listen('CONCIERGE.latest_booking', booking => {
            if (booking) {
                this.model.booking = booking;
            }
        });
    }

    public ngOnDestroy(): void {
        super.ngOnDestroy();
        this.service.set('CONCIERGE.latest_booking', null);
    }

    /**
     * Edit the catering orders of the booking
     */
    public editCatering() {
        this.editDetails('catering');
    }

    /**
     * Edit space requirements of the booking
     */
    public editRequirements() {
        this.editDetails('equipment');
    }

    /**
     * Edit details of the booking
     * @param type Refine the details to edit
     */
    public editDetails(type?: 'catering' | 'equipment' | undefined) {
        const booking = this.booking || ({} as any);
        const spaces = booking.room_list ? booking.room_list : booking.room ? [booking.room] : [];
        let catering: any = {};
        if (booking.catering) {
            if (booking.catering.items) {
                const empty: any = {};
                empty[booking.room.id] = booking.catering;
                catering = empty;
            } else {
                catering = booking.catering;
            }
        }
        this.service.Overlay.openModal(
            'booking-form',
            {
                name: 'overlay',
                data: {
                    booking: {
                        ...booking,
                        room: spaces.map(i => ({ ...i, bookings: [] })),
                        equipment: (booking.notes || [])
                            .filter(i => i.type === 'equipment')
                            .reduce((a, v) => {
                                a[v.space] = v.message;
                                return a;
                            }, {}),
                        catering_notes: (booking.notes || [])
                            .filter(i => i.type === 'catering')
                            .sort((a, b) => b.date - a.date)
                            .reduce((a, v) => {
                                a[v.space] = v.message;
                                return a;
                            }, {}),
                        catering_code: Object.keys(catering).reduce((a, v) => {a[v] = catering[v].code; return a}, {}),
                        expected_attendees: {
                            ...(booking.expected_attendees || {})
                        },
                        old_date: dayjs(booking.date).valueOf(),
                        old_end: dayjs(booking.date).add(booking.duration, 'm').unix(),
                        booking_type: { id: this.type },
                        equipment_code: { ...(booking.equipment_code || {}) },
                        needs_catering: type !== 'equipment'&& catering[booking.room.id] && catering[booking.room.id].items,
                        catering: JSON.parse(JSON.stringify(catering)),
                        host: { ...(booking.organiser || {}) }
                    },
                    edit_catering: type === 'catering',
                    edit_equipment: type === 'equipment'
                }
            },
            e => {
                if (e.type !== 'cancel_close') {
                    e.close();
                }
            }
        );
    }

    /**
     * Accept the active booking
     */
    public acceptMeeting() {
        this.service.Bookings.accept(this.booking.id, {
            comment: '',
            room_id: this.booking.room ? this.booking.room.id : (this.event as any).room_id
        }).then(
            () => {
                this.service.success('Successfully accepted meeting');
                this.close();
            },
            e => {
                this.service.error(`Error accepting meeting: ${e}`);
            }
        );
    }

    /**
     * Decline the active booking
     */
    public declineMeeting() {
        this.service.Bookings.decline(this.booking.id, {
            booking_id: this.booking.id,
            organiser: this.booking.organiser.email,
            icaluid: this.booking.icaluid,
            start: Math.floor(this.booking.date / 1000),
            end: Math.floor(this.booking.date / 1000) + this.booking.duration * 60,
            room_id: this.booking.room ? this.booking.room.id : (this.booking as any).room_id
        }).then(
            () => {
                this.service.success('Successfully declined meeting');
                this.close();
            },
            e => {
                this.service.error(`Error declining meeting: ${e}`);
            }
        );
    }

    /**
     * Delete the active booking
     */
    public deleteMeeting() {
        const start = dayjs(this.booking.date);
        const end = start.add(this.booking.duration, 'm');
        this.service.Bookings.delete(this.booking.id, {
            concierge: true,
            host: this.booking.organiser.email,
            icaluid: this.booking.icaluid,
            start: start.unix(),
            end: end.unix()
        }).then(
            () => {
                this.close();
            },
            e => {
                this.service.error(`Error deleting meeting: ${e}`);
            }
        );
    }

    /**
     * Update private details for booking
     */
    public updateOffsets() {
        this.service.Overlay.openModal(
            'private-details',
            {
                data: {
                    booking: this.booking
                }
            },
            event => {
                if (event.type === 'finish') {
                }
                event.close();
            }
        );
    }

    public viewOrder() {
        if (sessionStorage) {
            sessionStorage.setItem('CONCIERGE.view_order', JSON.stringify({ ...this.booking, room: { ...this.booking.room, bookings: [] } }))
        }
        this.close();
    }

    /**
     * Delayed closing of the modal
     */
    public timedClose() {
        this.timeout('close', () => this.close(), 100);
    }

    /**
     * Cancel the delayed closing of the modal
     */
    public cancelClose() {
        this.timeout('cancel_close', () => this.clearTimer('close'), 50);
    }
}
