import { Component, OnInit } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';

import { Utils } from '../../shared/utility.class';
import { IBooking, IBookingNote } from '../../services/data/bookings.service';
import { AppService } from '../../services/app.service';

import * as dayjs from 'dayjs';

@Component({
    selector: 'app-private-details-modal',
    templateUrl: './private-details-modal.component.html',
    styleUrls: ['./private-details-modal.component.scss']
})
export class PrivateDetailsModalComponent extends OverlayContentComponent<AppService> {

    /** Availble duration values for the setup and breakdown times */
    public durations: string[] = Array(13).fill(0).map((_, idx) => Utils.humaniseDuration(idx * 5 || 0) || '0 minutes');

    /** Index of the active setup duration */
    public setup_index: number;
    /** Index of the active breakdown duration */
    public breakdown_index: number;
    /** Private notes for the booking */
    public notes: string;
    /** Whether private details are being saved */
    public loading: boolean;

    /** Active booking */
    public get booking(): IBooking {
        return this.model.booking || {} as any;
    }
    /** Setup value for the current booking */
    public get setup(): number {
        return this.booking.setup || 0;
    }
    /** Breakdown value for the current booking */
    public get breakdown(): number {
        return this.booking.breakdown || 0;
    }

    public init() {
        this.setup_index = Math.round(this.setup / 5);
        this.breakdown_index = Math.round(this.breakdown / 5);
        this.notes = this.booking.notes.find(note => note.type === 'private').message;
    }

    /**
     * Save changes to the private details
     */
    public save() {
        this.loading = true;
        // Do nothing if there are no changes
        if (this.setup_index * 5 === this.setup && this.breakdown_index === this.breakdown && !this.notes) {
            this.service.success('Successfully updated private details');
            this.event('finish');
            return;
        }
        const notes: IBookingNote[] = [...this.booking.notes, {
            type: 'private',
            date: dayjs().valueOf(),
            author: this.service.Users.current().name,
            space: this.booking.room.id,
            message: this.notes
        }];
        this.service.Bookings.updateItem(this.booking.id, {
            ...this.booking,
            room: this.booking.room_list || [this.booking.room],
            notes,
            setup: this.setup_index * 5,
            breakdown: this.breakdown_index * 5
        }).then((booking) => {
            this.service.set('CONCIERGE.latest_booking', booking);
            this.service.success('Successfully updated private details');
            this.event('finish');
        }, (err) => {
            this.loading = false;
            this.service.error(`Error updating private details: ${err}`);
        });
    }

}


