import { Component, OnInit } from '@angular/core';

import { BaseComponent } from '../../shared/globals/base.component';
import { AppService } from '../../services/app.service';

import * as moment from 'moment';
import { IBooking } from '../../services/data/bookings.service';
import { IRoom } from '../../services/data/rooms.service';

@Component({
    selector: 'app-week-view',
    templateUrl: './week-view.template.html',
    styleUrls: ['./week-view.styles.scss']
})
export class WeekViewComponent extends BaseComponent implements OnInit {
    public model: any = { settings: {} };

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.model.days = [];
        this.service.title = 'Week View';
        this.subscription('date', this.service.listen('APP.date', (date) => {
            const old_date = moment(this.model.date || null);
            old_date.add(-old_date.day(), 'd').hours(0).minutes(0).seconds(0).millisecond(0);
            const old_week_end = moment(old_date).add(6, 'days').hours(23).minutes(59).seconds(59);
            this.model.date = date;
            const new_date = moment(date || null);
            this.model.selected = null;
            if (!new_date.isBetween(old_date, old_week_end, 'm', '[]')) {
                this.updateBookings();
            } else {
                this.updateActiveDate();
            }
        }));
        this.subscription('level', this.service.listen('APP.level', (lvl) => {
            this.model.level = lvl;
            this.model.selected = false;
            this.updateBookings();
        }));
        this.subscription('room_type', this.service.listen('APP.room_type', (type) => {
            this.model.room_type = type || -1;
            this.updateBookings();
        }));
        this.generateTimeBlocks();
        this.interval('bookings', () => this.updateBookings(), 5 * 60 * 1000);
    }

    public generateTimeBlocks() {
        // Get settings
        const start = this.model.settings.start_hour || 7;
        const end = this.model.settings.end_hour || 21;
        const block_size = this.model.settings.block_size || 60;
        // Generate times
        const start_time = moment().hours(start % 24).minutes((start % 1) * 60).seconds(0);
        const time = moment(start_time);
        const end_time = moment(start_time).hours(end % 24).minutes((end % 1) * 60);
        const length = Math.abs(moment.duration(start_time.diff(end_time)).asMinutes());
        // Generate time blocks
        this.model.blocks = [];
        for (; time.isSameOrBefore(end_time, 'm'); time.add(block_size, 'm')) {
            this.model.blocks.push({
                id: time.format('H:00'),
                display: time.format('h:00 A'),
                offset: Math.abs(moment.duration(start_time.diff(time)).asMinutes()) / length,
                length: block_size / length,
                hour: time.minutes() === 0,
                hour_value: time.format('HH'),
                raw: time.valueOf()
            });
        }
        this.updateBookings();
    }

    public updateBookings() {
        this.timeout('update', () => {
            const date = moment(this.model.date || null);
            const week_start = moment(date).startOf('week');
            const week_end = moment(week_start).endOf('week');
            const day = moment(week_start);
            this.model.days = [];
            for (; day.isSameOrBefore(week_end, 'd'); day.add(1, 'd')) {
                this.model.days.push({
                    time: day.valueOf(),
                    display: day.format('ddd Do MMM'),
                    active: day.isSame(date, 'd'),
                    weekend: day.day() === 0 || day.day() === 6,
                    bookings: []
                });
            }
            this.model.update_level = this.model.level;
            this.model.loading = true;
            const zones = [];
            if (this.model.level !== -1) {
                zones.push(this.model.level);
            }
            this.service.Rooms.query({
                zone_ids: zones.reduce((a, i) => a += a ? ',' + i : i, '')
                    || this.service.Buildings.current().id,
                available_from: week_start.unix(),
                available_to: week_end.unix()
            }).then((rooms: IRoom[]) => {
                const type = this.model.room_type;
                const room_list = rooms.filter(i => type && type !== -1 ? i.zones.indexOf(type) >= 0 : true);
                this.service.Rooms.clearTimeline(week_start.valueOf(), week_end.valueOf());
                for (const rm of room_list) {
                    this.service.Rooms.updateTimeline(rm.id, rm.bookings);
                    for (const d of this.model.days) {
                        const time = moment(d.time).format('DD MMM YYYY');
                        if (rm.timeline[time]) {
                            const timeline = rm.timeline[time];
                            for (const bkn of timeline) {
                                let found = false;
                                for (const booking of d.bookings) {
                                    if (booking.id === bkn.id) {
                                        found = true;
                                        break;
                                    }
                                }
                                if (!found) {
                                    d.bookings.push(bkn);
                                }
                            }
                        }
                    }
                }
                for (const d of this.model.days) {
                    d.bookings = this.getBookingsForDate(d.time, d.bookings || []);
                }
                this.model.loading = false;
            }, () => this.model.loading = false);
        });
    }

    public getBookingsForDate(date: number, bookings: IBooking[]) {
        const day = moment(date);
        let end = null;
        bookings = (bookings as any).flat();
        const used_ids = [];
        const bkn_list = [];
        for (const blk of (this.model.blocks || [])) {
            day.hour(+blk.hour_value);
            end = moment(day).add(1, 'h');
            let bkns: any = [];
            for (const bkn of bookings) {
                const start = moment(bkn.date);
                if (start.isBetween(day, end, 'm', '[)') && used_ids.indexOf(bkn.id) < 0) {
                    bkns.push(bkn);
                    used_ids.push(bkn.id);
                }
            }
            bkns = bkns.flat();
            bkns.sort((a, b) => a.date - b.date || a.duration - b.duration);
            bkn_list.push(bkns);
        }
        return bkn_list;
    }

    private updateActiveDate() {
        const date = moment(this.model.date || null);
        for (const day of this.model.days) {
            day.active = moment(day.time).isSame(date, 'd');
        }
    }

    public selectBlock(item, index, block) {
        this.model.selected = item.bookings[index];
        const hours = moment(block.raw).hours();
        const minutes = moment(block.raw).minutes();
        this.model.time = moment(item.time).hours(hours).minutes(minutes);
    }

}
