
import { Component } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';

import { BaseComponent } from '../../../shared/globals/base.component';
import { AppService } from '../../../services/app.service';


@Component({
    selector: 'feedback',
    templateUrl: './feedback.template.html',
    styleUrls: ['./feedback.styles.scss']
})
export class FeedbackComponent extends BaseComponent {

    public model: any = {};

    constructor(private service: AppService, private router: Router) {
        super();
        this.timeout('Getting Title', () => this.updateRoute(document.title), 1000);
        router.events
            .subscribe((event:NavigationEnd) => {
                this.timeout('Getting Title', () => this.updateRoute(document.title));
            });
    }

    public updateRoute(location) {
        location = location.substr(0, location.indexOf(" |"));
        this.model.current_page = location || 'Day View';
    }
}
