import { Component, Input } from '@angular/core';

import { IBooking } from '../../../services/data/bookings.service';
import { BaseComponent } from '../../../shared/globals/base.component';
import { IUser } from '../../../services/data/users.service';

import * as dayjs from 'dayjs'
import { Utils } from '../../../shared/utility.class';

@Component({
    selector: 'meeting-details-display',
    templateUrl: './details-display.component.html',
    styleUrls: ['./details-display.component.scss']
})
export class MeetingDetailsDisplayComponent extends BaseComponent {
    @Input() public booking: IBooking;

    /** Host of the booking */
    public get host(): IUser {
        const booking: IBooking = this.booking || {} as any;
        return booking.organiser;
    }

    /** Display date for the booking */
    public get start(): string {
        const booking: IBooking = this.booking || {} as any;

        const date = dayjs(booking.date);
        return (booking.all_day ? date.startOf('d') : date).format('DD MMM YYYY, h:mm A');
    }

    /** Display date for the booking */
    public get end(): string {
        const booking: IBooking = this.booking || {} as any;
        const date = dayjs(booking.date).add(booking.duration, 'm');
        return (booking.all_day ? date.startOf('d') : date).format('DD MMM YYYY, h:mm A');
    }

    /** Display string for the booking duration */
    public get duration(): string {
        const booking: IBooking = this.booking || {} as any;
        return Utils.humaniseDuration(booking.duration);
    }

    /** List of attendees for the bookoing */
    public get attendees(): IUser[] {
        const booking: IBooking = this.booking || {} as any;
        return booking.attendees || [];
    }

    /** Description of the booking */
    public get description(): string {
        const booking: IBooking = this.booking || {} as any;
        const notes = booking.notes || [];
        const description = notes.find(i => i.type === 'description');
        return (description ? description.message : null) || booking.description || '';
    }

    /** List of space names */
    public get locations(): string[] {
        const booking: IBooking = this.booking || {} as any;
        const spaces = booking.room_list
            ? booking.room_list
            : (booking.room ? [booking.room] : []);
        return spaces.map(i => i.name);
    }

    /** Recurrence setting for booking */
    public get recurrence(): string {
        return 'No Recurrence';
    }

}
