
import { Component, OnInit, Input, SimpleChanges, OnChanges, Output, EventEmitter } from '@angular/core';

import { BaseComponent } from '../../../shared/globals/base.component';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';
import { IRoom } from '../../../services/data/rooms.service';
import { IBuilding } from '../../../services/data/buildings.service';
import { AppService } from '../../../services/app.service';

@Component({
    selector: 'booking-flow-equipment-details',
    templateUrl: './equipment-details.component.html',
    styleUrls: ['./equipment-details.component.scss']
})
export class BookingEquipmentDetailsComponent extends BaseComponent implements OnChanges {
    @Input('spaces') public spaces: IRoom[];
    @Input('notes') public _notes: ADynamicFormField;
    @Input('costCode') public _cost_code: ADynamicFormField;
    @Input('expected') public _expected: ADynamicFormField;
    /** Whether form fields is valid */
    @Input() public valid: boolean;
    /** Emitter for valid state of the form fields */
    @Output() public validChange = new EventEmitter<boolean>();
    /** ID of the active space */
    public active_space: string = 'default';
    /** Mapping of spaces to form errors */
    public errors: { [space_id: string]: { charge_code: boolean, expected_attendees: boolean } } = {};

    constructor(private _service: AppService) {
        super();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        console.log('Details:', this);
        if (changes.spaces && this.spaces && this.spaces.length > 0) {
            const space = this.spaces.find(i => i.id === this.active_space);
            if (!space) {
                this.active_space = this.spaces[0].id;
            }
        }
        if (changes.valid && this.valid) {
            this.timeout('validate', () => {
                this.isValid()
                    ? this.validChange.emit(true)
                    : this.updateErrors();
            }, 10);
        }
    }

    /** Whether form has errors */
    public get has_errors(): boolean {
        return Object.keys(this.errors).length > 0;
    }

    /** Equipment notes for the meeting */
    public get notes(): { [space_id: string]: string } {
        if (this._notes && this._notes.control) {
            const value = this._notes.control.value;
            if (!value) {
                this._notes.setValue({});
            }
            return this._notes.control.value;
        }
        return {};
    }

    /** Equipment cost code for the meeting */
    public get cost_code(): { [space_id: string]: string } {
        if (this._cost_code && this._cost_code.control) {
            const value = this._cost_code.control.value;
            if (!value) {
                this._cost_code.setValue({});
            }
            return this._cost_code.control.value;
        }
        return {};
    }

    /** Expected number of attendees for spaces */
    public get expected_attendees(): { [space_id: string]: number } {
        if (this._expected && this._expected.control) {
            const value = this._expected.control.value;
            if (!value) {
                this._expected.setValue({});
            }
            return this._expected.control.value;
        }
        return {};
    }

    /** Whether the active space requires a charge code */
    public get requires_code(): boolean {
        return this.isCodeRequired(this.active_space);
    }

    /** Whether the active space requires expected attendees */
    public get requires_expected_attendees(): boolean {
        return this.isExpectedAttendeesRequired(this.active_space);
    }

    /**
     * Update form errors
     */
    private updateErrors() {
        this.errors = {};
        return this.spaces.forEach((space) => {
            const code_error = this.isCodeRequired(space.id) && !this.cost_code[space.id];
            const attendee_error = this.isExpectedAttendeesRequired(space.id) && !this.expected_attendees[space.id];
            if (code_error || attendee_error) {
                this.errors[space.id] = {
                    charge_code: code_error,
                    expected_attendees: attendee_error
                };
            }
        });
    }

    /**
     * Whether current state of the form is valid for the selected spaces
     */
    private isValid(): boolean {
        return this.spaces.reduce((valid, space) => {
            return (
                valid &&
                (!this.isCodeRequired(space.id) ||
                    !!this.cost_code[space.id]) &&
                (!this.isExpectedAttendeesRequired(space.id) ||
                    !!this.expected_attendees[space.id])
            );
        }, true);
    }


    /**
     * Whether the given space requires a charge code
     * @param space_id ID of the space to check
     */
    private isCodeRequired(space_id: string) {
        const bld = this.buildingForSpace(space_id);
        if (!bld) {
            return false;
        }
        return bld.required.equipment_code;
    }

    /**
     * Whether the give space requires expected attendees
     * @param space_id ID of the space to check
     */
    private isExpectedAttendeesRequired(space_id: string) {
        const bld = this.buildingForSpace(space_id);
        if (!bld) {
            return false;
        }
        return bld.required.expected_attendees;
    }

    /**
     * Get the building for the space with the given ID
     * @param space_id ID of the space to check
     */
    private buildingForSpace(space_id: string): IBuilding {
        const space = this.spaces.find(space => space.id === space_id);
        if (!space || !space.level) {
            return null;
        }
        return this._service.Buildings.get(space.level.bld_id);
    }
}

