
import { Component } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { AppService } from '../../services/app.service';
import { ICateringOrderDisplay } from '../../shell/catering/components/orders/orders.component';
import { IBookingNote, IBooking } from '../../services/data/bookings.service';
import { IRoom } from '../../services/data/rooms.service';

import * as dayjs from 'dayjs';


@Component({
    selector: 'catering-details-overlay',
    templateUrl: './catering-details-overlay.component.html',
    styleUrls: ['./catering-details-overlay.component.scss']
})
export class CateringDetailsOverlayComponent extends OverlayContentComponent<AppService> {

    public model: { [name: string]: any } = {};
    /** New note to add to private notes */
    public new_note: string;
    /** Whether the order is being edited */
    public edit: boolean;
    /** Whether changes to order are being processed */
    public loading: boolean;
    /** Whether note additions are being processed */
    public loading_notes: boolean;
    /** List of form fields */
    public form_field: ADynamicFormField;
    /** List of available status */
    public orders_statuses: string[] = ["accepted", "preparing", "ready", "delivered", "cancelled"];

    /** Order to display on the modal */
    public get order(): ICateringOrderDisplay {
        return this.model.order || {} as any;
    }

    /** Room for the current order */
    public get space(): IRoom {
        const booking = this.order.booking || {} as any;
        return booking.room || {};
    }

    /** Booking associated with the order */
    public get booking(): IBooking {
        return this.order.booking || {} as any;
    }

    /** Private notes for the current order */
    public get private_notes(): IBookingNote[] {
        const booking: IBooking = this.order.booking || {} as any;
        const notes = (booking.notes || []);
        const list = (notes instanceof Array ? notes.filter(i => i.type === 'catering' && (!i.space || i.space === this.space.id)): null) || [];
        const now = dayjs();
        list.forEach(note => {
            const date = dayjs(note.date);
            note.time = date.format(date.isSame(now, 'd') ? 'h:mm A' : 'DD MMM YY');
        });
        return list;
    }

    /** Start date and time of the meeting associated with the order */
    public get time(): string {
        const order: ICateringOrderDisplay = this.model.order;
        const date = dayjs(order.date);
        return `(${date.format('DD MMMM YYYY')}) ${
            order.booking.all_day || order.booking.duration > 23 * 60
                ? 'All Day'
                : date.format('h:mm A') + ' start'
        }`
    }
    /** Booking period for the meeting associated with the order */
    public get period(): string {
        const order: ICateringOrderDisplay = this.model.order;
        if (order.booking.all_day || order.booking.duration > 23 * 60) {
            return 'All Day';
        } else {
            const date = dayjs(order.date);
            const end = date.add(order.booking.duration, 'm');
            return `${date.format('h:mm A')} - ${end.format('h:mm A')}`;
        }
    }

    public init() {
        this.model.form = {};
        this.model.group = 0;
        // Grab the catering details for the order
        const value = this.order.booking.catering;
        // Create field for editing order details
        this.form_field = new ADynamicFormField({
            key: 'catering',
            type: 'custom',
            value
        });
    }

    /** Update the status of an order */
    public statusUpdate(status, order) {
        const old_status = order.status;
        order.status = status;
        this.model.show_dropdown = false;
        const booking = order.booking;
        booking.catering[booking.room.id] = { ...booking.catering[booking.room.id], order_status: status };
        this.model.order = this.order;
        this.event('updated');
        this.service.Bookings.updateItem(booking.id, booking).then(() => {
            this.model.order = this.order;
            this.event('updated');
        }, () => {
            this.service.error('Failed to update status of meeting order');
            order.status = old_status;
            this.model.order = this.order;
            this.event('updated');
        });
    }

    /**
     * Navigate to the day view page for the booking associated with the order
     */
    public toDayView() {
        const room = this.order.room;
        if (room && room.id) {
            if (room.level) {
                this.service.set('APP.level', room.level.id);
            }
            this.service.navigate('day-view', { id: this.order.booking.id, room_id: room.id });
            this.close();
        }
    }

    public saveNote() {
        if (this.new_note) {
            const user = this.service.Users.current();
            const booking = this.order.booking;
            const notes = booking.notes || [];
            const date = dayjs().startOf('m');
            booking.notes = [
                ...notes,
                {
                    type: 'catering',
                    date: date.valueOf(),
                    author: user.name,
                    space: booking.room.id,
                    message: this.new_note,
                    time: date.format('h:mm A')
                }
            ];
            this.new_note = '';
            const rooms = booking.room_list ? booking.room_list : [booking.room];
            this.loading_notes = true;
            this.service.Bookings.updateItem(booking.id, { ...booking, room: rooms }).then((booking) => {
                this.model.order = { ...this.order, booking };
                this.event('updated');
                this.loading_notes = false;
            }, () => {
                this.service.error('Failed to add note to meeting associated with order');
                booking.notes = notes;
                this.loading_notes = false;
            });
        }
    }

    public saveOrder() {
        this.loading = true;
        const booking = this.order.booking;
        const old_order: any = (booking.catering
            ? booking.catering.items
                ? booking.catering[booking.room.id]
                : booking.catering
            : null) || {};
        const catering_map = this.form_field.getValue();
        const order = catering_map;
        if (order){
            booking.catering = order;
            this.service.Bookings.updateItem(booking.id, {
                ...booking, room: booking.room_list || [booking.room],
            }).then(() => {
                this.loading = false;
                this.edit = false;
                this.service.success('Successfully updated catering order');
                this.model.order = this.order;
                this.event('updated');
            }, () => {
                this.loading = false;
                this.service.error('Failed to update catering order');
                booking.catering = old_order;
            });
        }
    }

    public resize() {
        super.resize();
        this.model.vh = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
    }
}
