import { Component, ViewContainerRef } from '@angular/core';
import { OverlayService } from '@acaprojects/ngx-widgets';
import { ViewEncapsulation } from '@angular/core';
import { UploadManager, Amazon, Md5Workers } from '@acaprojects/ngx-uploads';

import { environment } from '../environments/environment';
import { ComposerService } from '@acaprojects/ngx-composer';
import { BaseComponent } from './shared/globals/base.component';
import { Subscription } from 'rxjs';

@Component({
    selector: 'app-root',
    template: `
        <div class="app">
            <router-outlet></router-outlet>
        </div>
    `,
    styleUrls: ['./app.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class AppComponent extends BaseComponent {

    constructor(
        private view: ViewContainerRef,
        private overlay: OverlayService,
        private uploads: UploadManager,
        private _md5_workers: Md5Workers,
        private _composer: ComposerService
    ) {
        super();
        this.overlay.view = view;
        (window as any).app_loaded = true;
        if (environment.production && 'serviceWorker' in navigator) {
            navigator.serviceWorker
                .getRegistration()
                .then(active => !active && navigator.serviceWorker.register('ngsw-worker.js'))
                .catch(console.error);
        }
        this._md5_workers.setup('assets/md5_worker.js');
        this.initUploads();
    }

    private initUploads() {
        this.subscription('composer', this._composer.initialised.subscribe(state => {
            if (state) {
                this.timeout('init_uploads', () => {
                    const token = this._composer.auth.token;
                    if (!token) {
                        return setTimeout(() => this.initUploads(), 300);
                    }
                    this.uploads.token = token;
                    this.uploads.autoStart = true;
                    this.uploads.endpoint = '/api/staff/uploads';
                    UploadManager.addProvider(Amazon);
                });
                this.unsub('composer');
            }
        }));
    }
}
