import { Injectable } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { SwUpdate } from '@angular/service-worker';
import { BehaviorSubject } from 'rxjs';

import { ComposerService } from '@acaprojects/ngx-composer';
import { OverlayService, NotificationComponent } from '@acaprojects/ngx-widgets';
import { ComposerOptions } from '@acaprojects/ts-composer';

import { environment } from '../../environments/environment.prod';

import { SettingsService } from './settings.service';
import { AnalyticsService } from './data/analytics.service';
import { Utils } from '../shared/utility.class';

import { UsersService } from './data/users.service';
import { BuildingsService } from './data/buildings.service';
import { CommentsService } from './data/comments.service';
import { RoomsService } from './data/rooms.service';
import { BookingsService } from './data/bookings.service';
import { VisitorsService } from './data/visitors.service';
import { HotkeyService } from './hotkey.service';
import { LocationService } from './data/location.service';
import { ContactsService } from './data/contacts.service';
import { CateringItemsService } from './data/catering/catering-items.service';
import { CateringMenuService } from './data/catering/catering-menu.service';
import { ReportsService } from './data/reports.service';

// To be removed after back-end is updated
import { BookingsReportsService } from './data/bookingsreports.service';

import { ConfirmModalComponent } from '../overlays/confirm-modal/confirm-modal.component';
import { AttendeeModalComponent } from '../overlays/attendee-modal/attendee-modal.component';
import { CalendarModalComponent } from '../overlays/calendar-modal/calendar-modal.component';
import { CateringDetailsOverlayComponent } from '../overlays/catering-details-overlay/catering-details-overlay.component';
import { TimePeriodModalComponent } from '../overlays/time-period-modal/time-period-modal.component';
import { BookingModalComponent } from '../overlays/booking-modal/booking-modal.component';
import { UserSearchModalComponent } from '../overlays/user-search-modal/user-search-modal.component';
import { MeetingDetailsOverlayComponent } from '../overlays/meeting-details-overlay/meeting-details-overlay.component';
import { RoomSelectModalComponent } from '../overlays/room-select-modal/room-select-modal.component';
import { VisitorDetailsOverlayComponent } from '../overlays/visitor-details-overlay/visitor-details-overlay.component';
import { UserDetailsOverlayComponent } from '../overlays/user-details-overlay/user-details-overlay.component';
import { AddVisitorModalComponent } from '../overlays/add-visitor-modal/add-visitor-modal.component';
import { CateringCategoriesService } from './data/catering/catering-categories.service';
import { CateringModalComponent } from '../overlays/catering-modal/catering-modal.component';
import { ComposerSettings } from '../shared/utilities/settings.interface';
import { BaseClass } from '../shared/globals/base.class';
import { PrivateDetailsModalComponent } from '../overlays/private-details-modal/private-details-modal.component';
import { ConfirmDeleteModalComponent } from '../overlays/confirm-delete/confirm-delete.component';

@Injectable({
    providedIn: 'root'
})
export class AppService extends BaseClass {
    private api_base = 'api/staff';

    private subjects: any = {};
    private observers: any = {};

    private prev_route: string[] = [];
    private model: any = {};
    private timers: any = {};

    constructor(private _title: Title,
        private _cache: SwUpdate,
        private router: Router,
        private overlay: OverlayService,
        private analytics: AnalyticsService,
        private settings: SettingsService,
        private _composer: ComposerService,
        private hotkeys: HotkeyService,
        private buildings: BuildingsService,
        private bookings: BookingsService,
        private catering_items: CateringItemsService,
        private catering_categories: CateringCategoriesService,
        private catering_menu: CateringMenuService,
        private comments: CommentsService,
        private rooms: RoomsService,
        private users: UsersService,
        private location: LocationService,
        private visitors: VisitorsService,
        private contacts: ContactsService,
        private reports: ReportsService,
        private bookingsreports: BookingsReportsService
    ) {
        super();
            // Set parent service on child services
        this.buildings.parent = this.bookings.parent = this.rooms.parent = this.comments.parent = this;
        this.users.parent = this.analytics.parent = this.catering_items.parent = this.visitors.parent = this;
        this.location.parent = this.contacts.parent = this.catering_menu.parent = this.catering_categories.parent = this.reports.parent = this.bookingsreports.parent = this;
            // Register modals/overlay components
        this.overlay.registerService(this);
        this.overlay.setupModal('confirm', { cmp: ConfirmModalComponent });
        this.overlay.setupModal('attendees', { cmp: AttendeeModalComponent });
        this.overlay.setupModal('calendar', { cmp: CalendarModalComponent });
        this.overlay.setupModal('catering-details', { cmp: CateringDetailsOverlayComponent });
        this.overlay.setupModal('booking-form', { cmp: BookingModalComponent });
        this.overlay.setupModal('time-period', { cmp: TimePeriodModalComponent });
        this.overlay.setupModal('user-search', { cmp: UserSearchModalComponent });
        this.overlay.setupModal('meeting-details', { cmp: MeetingDetailsOverlayComponent });
        this.overlay.setupModal('room-select', { cmp: RoomSelectModalComponent });
        this.overlay.setupModal('meeting-details', { cmp: MeetingDetailsOverlayComponent });
        this.overlay.setupModal('visitor-details', { cmp: VisitorDetailsOverlayComponent });
        this.overlay.setupModal('user-details', { cmp: UserDetailsOverlayComponent });
        this.overlay.setupModal('set-host', { cmp: UserDetailsOverlayComponent });
        this.overlay.setupModal('select-room', { cmp: RoomSelectModalComponent });
        this.overlay.setupModal('add-visitors', { cmp: AddVisitorModalComponent });
        this.overlay.setupModal('catering', { cmp: CateringModalComponent });
        this.overlay.setupModal('private-details', { cmp: PrivateDetailsModalComponent });
        this.overlay.setupModal('confirm-delete', { cmp: ConfirmDeleteModalComponent });
        this.setupCache();
        this.init();
    }

    get endpoint() {
        const host = this.Settings.get('composer.domain');
        const protocol = this.Settings.get('composer.protocol');
        const port = ((protocol === 'https:') ? '443' : '80');
        const url = `${protocol || location.protocol}//${host || location.host}`;
        const endpoint = `${url}`;
        return endpoint;
    }

    get api_endpoint() {
        return `${this.endpoint}/${this.api_base}`;
    }

    public init(tries: number = 0) {
        if (!this.settings.setup) {
            if (tries === 5)  { this.settings.init(); }
            return setTimeout(() => this.init(), 500);
        }
        if (this.settings.get('debug')) { (window as any).application = this; }
        this.model.title = this.settings.get('app.title') || 'Angular Application';
        this.setupComposer();
        this.subscription('composer_init', this._composer.initialised.subscribe((state) => {
            if (state) {
                this.unsub('composer_init');
                let init_state = this._composer.auth.is_online;
                this.subscription('online_state', this._composer.auth.online_state.subscribe((state) => {
                    this.timeout('online_state', () => {
                        if (state !== init_state) {
                            init_state = state;
                            this.info(
                                state
                                    ? 'Reconnected to server.'
                                    : 'Connection to server lost. Limited functionality will be be available until it reconnects.'
                            );
                        }
                    }, 1000);
                }));
                this.analytics.init();
                // Initialise data services
                this.buildings.init();
                this.bookings.init();
                this.users.init();
                this.rooms.init();
                this.visitors.init();
            }
        }));
        NotificationComponent.timeout(5000);
    }

    public ready() {
        const mock = this.settings.get('mock') ? (window as any).backend : null;
        const app_ready = this.settings.setup && this.users.current() && this.buildings.current();
        return app_ready && (!mock || mock.is_loaded);
    }

    /**
     * Initialise the composer library comms
     */
    private setupComposer(): void {
        this.log('SYSTEM', 'Setup up composer...');
        // Get application settings
        const settings: ComposerSettings = this.settings.get('composer') || {};
        const protocol = settings.protocol || location.protocol;
        const host = settings.domain || location.hostname;
        const port = settings.port || location.port;
        const url = settings.use_domain ? `${protocol}//${host}:${port}` : location.origin;
        const route = settings.route || '';
        const mock = this.settings.get('mock');
        // Generate configuration object
        const config: ComposerOptions = {
            scope: 'public',
            host: `${host}:${port}`,
            auth_uri: `${url}/auth/oauth/authorize`,
            token_uri: `${url}/auth/token`,
            redirect_uri: `${location.origin}${route}/oauth-resp.html`,
            handle_login: !settings.local_login,
            mock
        };
        this._composer.setup(config);
    }

    /**
     * Listen to changes of given property
     * @param name Name of the property
     * @param next Callback for changes to properties value
     */
    public listen(name: string, next: (data: any) => void) {
        if (this.subjects[name]) {
            return this.observers[name].subscribe(next);
        } else {
                // Create new variable to store property's value
            this.subjects[name] = new BehaviorSubject<any>(this[name] instanceof Function ? null : this[name]);
            this.observers[name] = this.subjects[name].asObservable();
                // Create raw getter and setter for property
            if (!(this[name] instanceof Function)) {
                Object.defineProperty(this, name, {
                    get: () => this.get(name),
                    set: (v: any) => this.set(name, v)
                });
            }
            return this.observers[name].subscribe(next);
        }
    }

    /**
     * Get the current value of the given property
     * @param name Name of the property
     */
    public get(name: string) {
        return this.subjects[name] ? this.subjects[name].getValue() : null;
    }

    /**
     * Set the value of the given property
     * @param name Name of the property
     * @param value New value to assign to the property
     */
    public set(name: string, value: any) {
        if (this.subjects[name]) {
            this.subjects[name].next(value);
        } else {
                // Create new variable to store property's value
            this.subjects[name] = new BehaviorSubject<any>(value);
            this.observers[name] = this.subjects[name].asObservable();
                // Create raw getter and setter for property
            if (!(this[name] instanceof Function)) {
                Object.defineProperty(this, name, {
                    get: () => this.get(name),
                    set: (v: any) => this.set(name, v)
                });
            }
        }
    }

    get Analytics() { return this.analytics; }
    get Settings() {
        this.settings.get = (setting: string) => this.buildings.getSetting(setting) || this.settings.getSetting(setting);
        return this.settings;
    }
    get Overlay() { return this.overlay; }
    get Bindings() { return this._composer.bindings; }
    get Hotkeys() { return this.hotkeys; }
        // Getters for data/API services
    get Buildings() { return this.buildings; }
    get Bookings() { return this.bookings; }
    get CateringItems() { return this.catering_items; }
    get CateringCategories() { return this.catering_categories; }
    get CateringMenu() { return this.catering_menu; }
    get Rooms() { return this.rooms; }
    get Users() { return this.users; }
    get Visitors() { return this.visitors; }
    get Location() { return this.location; }
    get Contacts() { return this.contacts; }
    get Reports() { return this.reports; }
    get BookingsReports() { return this.bookingsreports; }

    /**
     * Set the page title
     * @param str New value to set the page title
     */
    set title(str: string) {
        if (!this.model.title) {
            this.model.title = this.settings.get('app.title') || '';
        }
        const title = `${str ? str : ''}${this.model.title ? ' | ' + this.model.title : ''}`;
        this._title.setTitle(title || this.settings.get('app.title'));
    }

    /**
     * Get the current page title
     */
    get title() {
        return this._title.getTitle();
    }

    /**
     * Open Terms and conditions modal
     */
    public viewTerms() {
        this.overlay.openModal('terms', {})
            .then((inst: any) => inst.subscribe((e) => e.close()));
    }

    public logout() {
        this.users.logout();
    }

    /**
     * Wrapper for Angular Router navigate method
     * @param path Path to navigate
     * @param query Query parameter to add to the route
     */
    public navigate(path: string | string[], query?: any) {
        let path_list = [];
        if (path instanceof Array) {
            path_list = path_list.concat(path);
        } else {
            path_list.push(path);
        }
        this.prev_route.push(this.router.url);
        // if (!this.systems.resources.authLoaded) {
        this.router.navigate(path_list, { queryParams: query });
        // } else {
        // this.router.navigate([path]);
        // }
    }

    /**
     * Return to the previous page
     */
    public back() {
        if (this.prev_route.length > 0) {
            this.navigate(this.prev_route.pop());
            this.prev_route.pop();
        } else {
            this.navigate('');
        }
    }

    /**
     * Log message to the console
     * @param type Message prefix
     * @param msg Message to emit
     * @param args Arguments to attach to log
     * @param stream Stream to output the message
     */
    public log(type: string, msg: string, args?: any, stream: string = 'debug') {
        this.settings.log(type, msg, args, stream);
    }

    /**
     * Open confirm modal
     * @param options Options to pass to the modal
     * @param next Callback for events on the modal
     */
    public confirm(options: any, next?: (event: any) => void) {
        this.Overlay.openModal('confirm', {
            data: options,
        }).then((inst: any) => {
            setTimeout(() => inst.cmp_ref.instance.close = () => this.Overlay.remove('root', inst.id), 1000);
            inst.subscribe((e) => {
                if (next) { next(e); }
            });
        });
    }

    /**
     * Create a new error notification
     * @param msg Message to display
     * @param action User action to display
     * @param event Callback for user action
     */
    public error(msg: string, action?: string, event?: () => void) {
        const message = msg ? msg : `Error`;
        this.overlay.notify('success', {
            html: `<div class="display-icon error" style="font-size:2.0em"></div><div class="msg">${message}</div>`,
            name: 'ntfy error',
            action
        });
    }

    /**
     * Create a new success notification
     * @param msg Message to display
     * @param action User action to display
     * @param event Callback for user action
     */
    public success(msg: string, action?: string, event?: () => void) {
        const message = msg ? msg : `Success`;
        this.overlay.notify('success', {
            html: `<div class="display-icon success" style="font-size:2.0em"></div><div class="msg">${message}</div>`,
            name: 'ntfy success',
            action
        }, event);
    }

    /**
     * Create a new informational notification
     * @param msg Message to display
     * @param action User action to display
     * @param event Callback for user action
     */
    public info(msg: string, action?: string, event?: () => void) {
        const message = msg ? msg : `Information`;
        this.overlay.notify('info', {
            html: `<div class="display-icon info" style="font-size:2.0em"></div></div><div class="msg">${message}</div>`,
            name: 'ntfy info',
            action
        }, event);
    }

    get iOS() {
        return Utils.isMobileSafari();
    }

    /**
     * Setup handler for cache change events
     */
    private setupCache() {
        this._cache.available.subscribe((event) => {
            const current = `current version is ${event.current.hash}`;
            const available = `available version is ${event.available.hash}`;
            this.settings.log('CACHE', `Update available: ${current} ${available}`);
            this.info('Newer version of the app is available', 'Refresh', () => this.activateUpdate());
        });
        setInterval(() => this._cache.checkForUpdate(), 5 * 60 * 1000);
    }

    /**
     * Update the cache and reload the page
     */
    private activateUpdate() {
        if (environment.production) {
            this._cache.activateUpdate().then(() => location.reload(true));
        }
    }

}
