import { Component } from "@angular/core";
import { OverlayContentComponent } from "@acaprojects/ngx-widgets";

@Component({
    selector: 'confirm-delete-modal',
    templateUrl: './confirm-delete.component.html',
    styleUrls: ['./confirm-delete.component.scss']
})
export class ConfirmDeleteModalComponent extends OverlayContentComponent {
    /** Whether item is being deleted */
    public loading: boolean;
    /** Whether item was successfully deleted */
    public success: boolean;

    /**
     * Delete the item
     */
    public delete(): void {
        this.loading = true;
        this.service.Bookings.deleteItem(this.model.id, this.model.fields).then(() => {
            this.success = true;
            this.loading = false;
            this.timeout('success', () => this.event('finish'), 3000);
        }, () => {
            this.loading = false;
            this.service.error('Error deleting booking');
        });
    }
}
