
import { Injectable } from '@angular/core';
import { ComposerService } from '@acaprojects/ngx-composer';

import { BaseService } from '../base.service';
import { ICateringMenuItem } from "./catering-items.service";


export interface ICateringMenuCategory {
    /** Database ID of the category */
    id: string;
    /** Display name for the category */
    name: string;
    /** Display description for the category */
    description: string;
    /** List of zones that the category associates with */
    zones: string[];
    /** List of available items in the category */
    items: (ICateringMenuItem | ICateringMenuCategory)[];
    /** Whether this category identifed a package(category group) */
    package: boolean;
    /** URL to a image for the category */
    src: string;
    /** Price of package per person */
    price?: number;
    /** Number of this item package in the order */
    amount?: number;
    /** Number of items to select from package options */
    must_select?: number;
    /** Minumum number of items required for category */
    minimum?: number;
    /** Minumum number of items required for category */
    maximum?: number;
    /** Type of preparations needed for category or sub items */
    catering_type?: 'kitchen' | 'pantry';
}


@Injectable({
    providedIn: 'root'
})
export class CateringCategoriesService extends BaseService<ICateringMenuItem> {

    constructor(protected _composer: ComposerService) {
        super(_composer);
        this.model.name = 'catering category/group';
        this.model.route = '/catering/category';
    }

    public query(): never {
        throw Error('No index endpoint for catering categories. Use catering menu service.');
    }

    public show(): never {
        throw Error('No show endpoint for catering categories. Use catering menu service.');
    }

    public format(raw: any) {
        const formatted = {
            ...raw,
            image_path: raw.src,
            parent_categories: raw.parent_categories || raw.parent,
            minimum_quantity: raw.minimum || raw.minimum_quantity,
            maximum_quantity: raw.maximum || raw.maximum_quantity
        };
        delete formatted.minimum;
        delete formatted.maximum;
        return formatted;
    }
}